html {
    scroll-behavior: smooth;  
}

.scroll__up {
    position: fixed;
    bottom: 20px;
    right: 20px; 
    background: none;
    border: none;

}
 .scroll__svg {
     fill: getColor(accent-secondary);
     transition: fill 250ms $animation-cubic;
     transition: .5s;
     &:hover,
     &:focus {
         filter: brightness(1.5);
         fill: getColor(accent);
         outline: none;
     }
 }

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  